export const routes = [
  {
    path: '/error',
    name: 'error',
    meta: {
      title: '出错啦'
    },
    component: resolve => {
      require(['@/views/error.vue'], resolve);
    }
  },
  {
    path: '/',
    component: resolve => {
      require(['@/views/index.vue'], resolve);
    },
    children: [
      {
        path: '/:pid',
        redirect: '/:pid/homepage'
      },
      {
        path: '/:pid/login',
        name: 'login',
        meta: {
          title: '登录'
        },
        component: resolve => {
          require(['@/views/login/login.vue'], resolve);
        }
      },
      {
        path: '/:pid/homepage',
        name: 'homepage',
        meta: {
          title: '首页'
        },
        component: resolve => {
          require(['@/views/homepage/index.vue'], resolve);
        }
      },
      {
        path: '/:pid/card_info',
        name: 'cardInfo',
        meta: {
          title: '流量查询'
        },
        component: resolve => {
          require(['@/views/card-info/index.vue'], resolve);
        }
      },
      {
        path: '/:pid/card_recharge',
        name: 'cardRecharge',
        meta: {
          title: '流量充值'
        },
        component: resolve => {
          require(['@/views/card-recharge/index.vue'], resolve);
        }
      },
      {
        path: '/:pid/diagnose',
        name: 'diagnose',
        meta: {
          title: '智能诊断'
        },
        component: resolve => {
          require(['@/views/diagnose/index.vue'], resolve);
        }
      },
      {
        path: '/:pid/card_used_info',
        name: 'cardUsedInfo',
        meta: {
          title: '历史订单'
        },
        component: resolve => {
          require(['@/views/card-used-info/index.vue'], resolve);
        }
      }
    ]
  }
];



