import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// Vuex定义
const store = new Vuex.Store({
  state: {
    isLogin: false,
    currentPid: undefined,
    isCallLogin: false,
    isOffhook: false,
    user: {},
    accessToken: '',
    activeNaviMenu: '', // 顶部菜单
    activeMenu: '', // 左侧导航
    org_list: [],
    user_auth_list: {},
    role: undefined,
    currentPlatform: {},
    currentPath: [] // 面包屑
  },
  mutations: {
    setAccessToken (state, accessToken) {
      state.accessToken = accessToken;
    },
    login (state) {
      state.isLogin = true;
    },
    setCurrentPid (state, currentPid) {
      state.currentPid = currentPid;
    },
    logout (state) {
      state.isLogin = false;
      state.user = {};
      state.accessToken = '';
      localStorage.clear();
      sessionStorage.clear();
    },
    setUser (state, user) {
      state.user = user;
    },
    setActiveMenu (state, activeMenu) {
      state.activeMenu = activeMenu;
    }
  }
});

export default store;



