import Vue from 'vue';
import VueResource from 'vue-resource';
import Store from '../store';

Vue.use(VueResource);

export const http = Vue.http;

function HttpException (message) {
  this.body = {
    meta: {
      message: message
    }
  };
}

http.interceptors.push((request, next) => {
  let token = Store.state.user.accessToken;
  if (token) {
    http.headers.common['Authorization'] = 'Bearer ' + token;
  } else {
    http.headers.common['Authorization'] = '';
  }
  next((response) => {
    // 返回流类不需要验证的请求
    if (response.headers.get('successVerify') === 'false') {
      return response;
    }
    // 拦截401
    if (response.body.status === 401) {
      Store.commit('logout');
      throw new HttpException('令牌过期');
    }
    if (!response.body.meta.success) {
      throw new HttpException(response.body.meta.message);
    }
    return response;
  });
});



